import React, { useState } from 'react'

import styles from './table.module.css'
import chev from '../../assets/icons/Chevron-Right.png'
import filter from '../../assets/icons/Filter.png'
import arrowUpIcon from '../../assets/icons/Arrow-Up.png'
import ClientFilterComponent from '../filters/client-filter/client-filter.component'
import StartsWithFilterComponent from '../filters/starts-with-filter/starts-with-filter.component'
import DurationFilterComponent from '../filters/duration-filter/duration-filter.component'
import StatusFilterComponent from '../filters/status-filter/status-filter.component'

function TableComponent(props) {
  const [showPageSizeOptions, setShowPageSizeOptions] = useState(false)
  const [showClearFilter, setShowClearFilter] = useState(false)
  const [showTitleFilter, setShowTitleFilter] = useState(false)
  const [showWorkerFilter, setShowWorkerFilter] = useState(false)
  const [showDurationFilter, setShowDurationFilter] = useState(false)
  const [showStatusFilter, setShowStatusFilter] = useState(false)
  const [selectedItem, setSelectedItem] = useState({
    workerId: null,
    client: '',
    title: '',
    worker: '',
    started: '',
    ended: '',
    duration: '',
    status: '',
  })

  const togglePageSizeOptions = () => {
    setShowPageSizeOptions(!showPageSizeOptions)
  }

  const handlePreviousPage = () => {
    if (props.currentPage > 1) {
      props.getData(props.currentPage - 1, props.pageSize)
    }
    props.decrementPageButtonNumbers()
  }

  const setCurrentPage = (page) => {
    props.getData(page, props.pageSize)
    props.paginateTo()
  }

  const handleNextPage = () => {
    if (props.currentPage < props.totalData / props.pageSize) {
      props.getData(props.currentPage + 1, props.pageSize)
    }
    props.incrementPageButtonNumbers()
  }

  const selectItem = (id) => {
    setSelectedItem(props.data[id])
  }

  const handlePageSize = (size) => {
    props.getData(1, size)
    setShowPageSizeOptions(false)
  }

  const toggleDropdown = (header) => {
    if (header === 'client') {
      setShowClearFilter(!showClearFilter)
    } else {
      setShowClearFilter(false)
    }

    if (header === 'title') {
      setShowTitleFilter(!showTitleFilter)
    } else {
      setShowTitleFilter(false)
    }

    if (header === 'worker') {
      setShowWorkerFilter(!showWorkerFilter)
    } else {
      setShowWorkerFilter(false)
    }

    if (header === 'duration') {
      setShowDurationFilter(!showDurationFilter)
    } else {
      setShowDurationFilter(false)
    }

    if (header === 'status') {
      setShowStatusFilter(!showStatusFilter)
    } else {
      setShowStatusFilter(false)
    }
  }

  return (
    <>
      <div className={styles.tableDiv}>
        <table>
          <thead>
            <tr>
              <td className={styles.thClient}>
                CLIENT{' '}
                <img
                  className={`${styles.filterIcons} mx-2`}
                  src={filter}
                  alt="FilterIcon1"
                  onClick={() => toggleDropdown('client')}
                />
                <div
                  className={styles.dropdownClient}
                  style={{ display: showClearFilter ? null : 'none' }}
                >
                  <ClientFilterComponent />
                </div>
              </td>
              <td className={styles.thTitle}>
                TITLE{' '}
                <img
                  className={`${styles.filterIcons} mx-2`}
                  src={filter}
                  alt="FilterIcon2"
                  onClick={() => toggleDropdown('title')}
                />
                <div
                  className={styles.dropdownTitle}
                  style={{ display: showTitleFilter ? null : 'none' }}
                >
                  <StartsWithFilterComponent />
                </div>
              </td>
              <td className={styles.thWorker}>
                WORKER{' '}
                <img
                  className={`${styles.filterIcons} mx-2`}
                  src={filter}
                  alt="FilterIcon3"
                  onClick={() => toggleDropdown('worker')}
                />
                <div
                  className={styles.dropdownWorker}
                  style={{ display: showWorkerFilter ? null : 'none' }}
                >
                  <StartsWithFilterComponent />
                </div>
              </td>
              <td className={styles.thStarted}>
                STARTED{' '}
                <img
                  className={`${styles.filterIcons} mx-2`}
                  src={filter}
                  alt="FilterIcon4"
                />
                <img src={arrowUpIcon} alt="ArrowUpIcon" />
              </td>
              <td className={styles.thEnded}>
                ENDED{' '}
                <img
                  className={`${styles.filterIcons} mx-1`}
                  src={filter}
                  alt="FilterIcon5"
                />
              </td>
              <td className={styles.thDuration}>
                DURATION{' '}
                <img
                  className={`${styles.filterIcons} mx-2`}
                  src={filter}
                  alt="FilterIcon6"
                  onClick={() => toggleDropdown('duration')}
                />
                <div
                  className={styles.dropdownDuration}
                  style={{ display: showDurationFilter ? null : 'none' }}
                >
                  <DurationFilterComponent />
                </div>
              </td>
              <td className={styles.thStatus}>
                STATUS{' '}
                <img
                  className={`${styles.filterIcons} mx-2`}
                  src={filter}
                  alt="FilterIcon"
                  onClick={() => toggleDropdown('status')}
                />
                <div
                  className={styles.dropdownStatus}
                  style={{ display: showStatusFilter ? null : 'none' }}
                >
                  <StatusFilterComponent />
                </div>
              </td>
            </tr>
          </thead>
          <tbody
            className={`${styles.tbody} ${styles.tbodyDiv}`}
            style={{ overflowY: props.data.length > 12 ? 'auto' : null }}
          >
            {props.data.map((item, index) => {
              const successClassName =
                item.status === 'Success' ? styles.successStatus : null
              const runningClassName =
                item.status === 'Running' ? styles.runningStatus : null
              const failedClassName =
                item.status === 'Failed' ? styles.failedStatus : null
              const stoppedClassName =
                item.status === 'Stopped' ? styles.stoppedStatus : null
              const queuedClassName =
                item.status === 'Queued' ? styles.queuedStatus : null
              return (
                <tr
                  className={
                    selectedItem.workerId === item.workerId
                      ? styles.activeRow
                      : null
                  }
                  key={index}
                  onClick={() => selectItem(index)}
                  style={{ cursor: 'pointer' }}
                >
                  <td className={styles.tdClient}>{item.client}</td>
                  <td className={styles.tdTitle}>
                    <div
                      style={{
                        whiteSpace: 'nowrap',
                        overflow: 'hidden',
                        width: '100%',
                      }}
                    >
                      {item.title}
                    </div>
                  </td>
                  <td className={styles.tdWorker}>{item.worker}</td>
                  <td className={styles.tdStarted}>{item.started}</td>
                  <td className={styles.tdEnded}>{item.ended}</td>
                  <td className={styles.tdDuration}>{item.duration}</td>
                  <td className={styles.tdStatus}>
                    <div
                      className={`${successClassName} ${runningClassName} ${failedClassName} ${stoppedClassName} ${queuedClassName}`}
                    >
                      {item.status}
                    </div>
                  </td>
                </tr>
              )
            })}
          </tbody>
        </table>
      </div>
      <div className={styles.tableFooter}>
        <div className="row">
          <div className="col-12">
            <label className={styles.label}>Listing</label>
            <span className="position-relative">
              <button
                className={styles.listSelection}
                onClick={togglePageSizeOptions}
              >
                {props.pageSize}
              </button>
              <div
                className={styles.pageSizeOptions}
                style={{ display: showPageSizeOptions ? null : 'none' }}
              >
                <div
                  className={styles.optionItem}
                  onClick={() => handlePageSize(12)}
                >
                  12
                </div>
                <div
                  className={styles.optionItem}
                  onClick={() => handlePageSize(15)}
                >
                  15
                </div>
                <div
                  className={styles.optionItem}
                  onClick={() => handlePageSize(50)}
                >
                  50
                </div>
                <div
                  className={styles.optionItem}
                  onClick={() => handlePageSize(100)}
                >
                  100
                </div>
              </div>
            </span>
            <label className={styles.label}>
              entries, showing 1 to {props.pageSize} of {props.totalData}
            </label>

            <div className="btn-group float-end" role="group">
              <button
                className={styles.paginationBtn}
                type="button"
                onClick={handlePreviousPage}
              >
                <img
                  className={styles.chevLeftIcon}
                  src={chev}
                  alt="Paginate-Left"
                />
              </button>
              {props.pageButtons.map((button, index) => {
                return (
                  <button
                    className={`${styles.paginationBtn} ${
                      button.buttonText === props.currentPage
                        ? styles.paginationActiveBtn
                        : null
                    }`}
                    type="button"
                    key={index}
                    onClick={() => setCurrentPage(button.buttonText)}
                  >
                    {button.buttonText}
                  </button>
                )
              })}
              <button
                className={styles.paginationBtn}
                type="button"
                onClick={handleNextPage}
              >
                <img
                  className={styles.chevRightIcon}
                  src={chev}
                  alt="Paginate-Right"
                />
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  )
}

export default TableComponent
