import { useState } from 'react'
import { Route } from 'react-router'
import { Switch, Redirect } from 'react-router-dom'

import authService from './core/auth.service'

import './App.css'
import './AppFont.css'
import 'bootstrap/dist/css/bootstrap.min.css'

import ViewsComponent from './views/views.component'
import LoginComponent from './components/log-in/log-in.component'
import ForgotPasswordComponent from './components/forgot-password/forgot-password.component'

function App() {
  const [isAuthenticated, setAuthentication] = useState(
    authService.isAuthenticated
  )

  const loginInEvent = () => {
    setAuthentication(authService.isAuthenticated)
  }

  const logoutEvent = () => {
    setAuthentication(authService.isAuthenticated)
  }

  return (
    <div className="App">
      {isAuthenticated ? (
        <>
          <ViewsComponent logoutEvent={logoutEvent} />
        </>
      ) : (
        <>
          <Switch>
            <Route
              exact
              path="/login"
              render={(props) => <LoginComponent loginInEvent={loginInEvent} />}
            />
            <Route
              exact
              path="/forgot-password"
              render={(props) => <ForgotPasswordComponent />}
            />
            <Route render={(props) => <Redirect to="/login" />} />
          </Switch>
        </>
      )}
    </div>
  )
}

export default App
