import React, { useState } from 'react'
import { Route, Switch, Redirect, useLocation } from 'react-router'

import MonitoringComponent from '../components/monitoring/monitoring.component'
import SidebarComponent from '../shared/sidebar/sidebar.component'
import NotFoundComponent from '../components/not-found/notFound.component'

import styles from './views.module.css'

function ViewsComponent(props) {
  const location = useLocation()
  const [sidebarState, setSidebarState] = useState()

  return (
    <div className={styles.views}>
      <div
        className={`${styles.sidebar} ${
          sidebarState ? styles.vaiShrinkSidebar : null
        }`}
        style={{
          display: location.pathname === '/not-found' ? 'none' : null,
        }}
      >
        <SidebarComponent
          toggleSidebar={(state) => setSidebarState(state)}
          logoutEvent={props.logoutEvent}
        />
      </div>
      <div
        className={styles.content}
        style={{
          display: location.pathname === '/not-found' ? 'none' : null,
        }}
      >
        <Route exact path="/monitoring" component={MonitoringComponent} />
      </div>
      <Switch>
        <Route
          exact
          path="/login"
          render={() => {
            return <Redirect to="/monitoring" />
          }}
        />
        <Route
          exact
          path="/monitoring"
          render={() => {
            return <Redirect to="/monitoring" />
          }}
        />
        <Route path="/not-found" component={NotFoundComponent} />
        <Route render={(props) => <Redirect to="/not-found" />} />
      </Switch>
    </div>
  )
}

export default ViewsComponent
