import React, { useState } from 'react'
import styles from './starts-with-filter.module.css'

function StartsWithFilterComponent() {
  const [buttonText, setButtonText] = useState('Starts With')
  const [showOptions, setShowOptions] = useState(false)

  const toggleOptions = () => {
    setShowOptions(!showOptions)
  }

  const selectOption = (text) => {
    setButtonText(text)
    setShowOptions(false)
  }

  return (
    <div className="p-3">
      <div></div>
      <div className="position-relative">
        <button className={styles.startsWithButton} onClick={toggleOptions}>
          {buttonText}
        </button>
        <div
          className={styles.options}
          style={{ display: showOptions ? null : 'none' }}
        >
          <div
            className={styles.optionItem}
            onClick={() => selectOption('Starts With')}
          >
            <span>Starts With</span>
          </div>
          <div
            className={styles.optionItem}
            onClick={() => selectOption('Contains With')}
          >
            <span>Contains With</span>
          </div>
        </div>
      </div>
      <br />
      <input
        className={styles.startsWithInput}
        type="text"
        placeholder="Enter the value"
      />
      <hr style={{ backgroundColor: '#6d6d73' }} />
      <div className="d-flex">
        <p className={styles.clearFilterButton}>Clear</p>
        <button className={styles.filterButton}>Filter</button>
      </div>
    </div>
  )
}

export default StartsWithFilterComponent
